'use client';

import { useState } from 'react';

export default function ContactPage() {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: ''
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitMessage, setSubmitMessage] = useState('');

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    
    // Here you would typically send the form data to your API
    setTimeout(() => {
      setSubmitMessage('Thank you for your message! We\'ll get back to you soon.');
      setFormData({ name: '', email: '', subject: '', message: '' });
      setIsSubmitting(false);
      
      setTimeout(() => {
        setSubmitMessage('');
      }, 5000);
    }, 1000);
  };

  return (
    <div className="container-custom">
      <section style={{ marginTop: '40px', marginBottom: '60px' }}>
        <h1 style={{ marginBottom: '40px' }}>CONTACT</h1>
        
        <div style={{ marginLeft: '69px', marginRight: '69px' }}>
          <div style={{ 
            display: 'grid', 
            gridTemplateColumns: '1fr 1fr', 
            gap: '60px'
          }}>
            {/* Contact Info */}
            <div>
              <h2 style={{
                fontSize: '2rem',
                fontFamily: "'dinBoldFont', sans-serif",
                marginBottom: '30px',
                backgroundImage: 'none',
                padding: 0
              }}>
                Get In Touch
              </h2>
              
              <div style={{ marginBottom: '30px' }}>
                <h3 style={{
                  fontSize: '1.6rem',
                  fontFamily: "'dinFont', sans-serif",
                  marginBottom: '10px',
                  color: '#f8ec21'
                }}>
                  Email
                </h3>
                <p style={{ fontSize: '1.4rem' }}>info@listedproductions.com</p>
                <p style={{ fontSize: '1.4rem' }}>bookings@listedproductions.com</p>
              </div>
              
              <div style={{ marginBottom: '30px' }}>
                <h3 style={{
                  fontSize: '1.6rem',
                  fontFamily: "'dinFont', sans-serif",
                  marginBottom: '10px',
                  color: '#f8ec21'
                }}>
                  Location
                </h3>
                <p style={{ fontSize: '1.4rem' }}>San Francisco, CA</p>
                <p style={{ fontSize: '1.4rem' }}>United States</p>
              </div>
              
              <div style={{
                padding: '20px',
                backgroundColor: 'rgba(0,0,0,0.5)',
                border: '1px solid rgba(255,255,255,0.1)'
              }}>
                <h3 style={{
                  fontSize: '1.6rem',
                  fontFamily: "'dinFont', sans-serif",
                  marginBottom: '10px'
                }}>
                  Booking Inquiries
                </h3>
                <p style={{ fontSize: '1.3rem', color: 'rgba(255,255,255,0.7)' }}>
                  For artist bookings and event partnerships, please include event details, dates, and budget in your message.
                </p>
              </div>
            </div>

            {/* Contact Form */}
            <div>
              <h2 style={{
                fontSize: '2rem',
                fontFamily: "'dinBoldFont', sans-serif",
                marginBottom: '30px',
                backgroundImage: 'none',
                padding: 0
              }}>
                Send Message
              </h2>
              
              <form onSubmit={handleSubmit}>
                <div style={{ marginBottom: '20px' }}>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    placeholder="Your Name *"
                    value={formData.name}
                    onChange={handleChange}
                    required
                    style={{
                      width: '100%',
                      padding: '12px',
                      backgroundColor: 'rgba(255,255,255,0.1)',
                      border: '1px solid rgba(255,255,255,0.2)',
                      color: 'white',
                      fontSize: '1.4rem',
                      outline: 'none'
                    }}
                  />
                </div>
                
                <div style={{ marginBottom: '20px' }}>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    placeholder="Your Email *"
                    value={formData.email}
                    onChange={handleChange}
                    required
                    style={{
                      width: '100%',
                      padding: '12px',
                      backgroundColor: 'rgba(255,255,255,0.1)',
                      border: '1px solid rgba(255,255,255,0.2)',
                      color: 'white',
                      fontSize: '1.4rem',
                      outline: 'none'
                    }}
                  />
                </div>
                
                <div style={{ marginBottom: '20px' }}>
                  <select
                    id="subject"
                    name="subject"
                    value={formData.subject}
                    onChange={handleChange}
                    required
                    style={{
                      width: '100%',
                      padding: '12px',
                      backgroundColor: 'rgba(255,255,255,0.1)',
                      border: '1px solid rgba(255,255,255,0.2)',
                      color: 'white',
                      fontSize: '1.4rem',
                      outline: 'none'
                    }}
                  >
                    <option value="" style={{ backgroundColor: '#000' }}>Select Subject *</option>
                    <option value="booking" style={{ backgroundColor: '#000' }}>Artist Booking</option>
                    <option value="event" style={{ backgroundColor: '#000' }}>Event Partnership</option>
                    <option value="press" style={{ backgroundColor: '#000' }}>Press Inquiry</option>
                    <option value="general" style={{ backgroundColor: '#000' }}>General Inquiry</option>
                  </select>
                </div>
                
                <div style={{ marginBottom: '20px' }}>
                  <textarea
                    id="message"
                    name="message"
                    placeholder="Your Message *"
                    value={formData.message}
                    onChange={handleChange}
                    required
                    rows={6}
                    style={{
                      width: '100%',
                      padding: '12px',
                      backgroundColor: 'rgba(255,255,255,0.1)',
                      border: '1px solid rgba(255,255,255,0.2)',
                      color: 'white',
                      fontSize: '1.4rem',
                      outline: 'none',
                      resize: 'vertical'
                    }}
                  />
                </div>
                
                <button
                  type="submit"
                  disabled={isSubmitting}
                  style={{
                    padding: '12px 40px',
                    backgroundColor: '#f8ec21',
                    color: '#000',
                    fontSize: '1.4rem',
                    fontWeight: 'bold',
                    textTransform: 'uppercase',
                    border: 'none',
                    cursor: isSubmitting ? 'not-allowed' : 'pointer',
                    opacity: isSubmitting ? 0.7 : 1
                  }}
                >
                  {isSubmitting ? 'Sending...' : 'Send Message'}
                </button>
                
                {submitMessage && (
                  <div style={{
                    marginTop: '20px',
                    padding: '15px',
                    backgroundColor: 'rgba(0,255,0,0.1)',
                    border: '1px solid rgba(0,255,0,0.3)',
                    color: '#12b258',
                    fontSize: '1.4rem'
                  }}>
                    {submitMessage}
                  </div>
                )}
              </form>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}